/*************Modal************/

.space-between{
  justify-content: space-between;
}

.flex-end{
  display: flex;
  justify-content: flex-end;
}

.justify-center{
  display: flex;
  justify-content: center;
}

.noscroll { overflow: hidden; }

.modal{
  display:none;
}

.modal.active{
  display:block;
}

.modal {
  position: fixed;
  z-index: 10000; /* 1 */
  top: 0;
  left: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
}

.modal-overlay {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: hsla(0, 0%, 0%, 0.5);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s linear 0.3s, opacity 0.3s;
}

.modal.is-visible {
  visibility: visible;
}

.modal.is-visible .modal-overlay {
  opacity: 1;
  visibility: visible;
  transition-delay: 0s;
}

.modal.is-visible .modal-transition {
  transform: translateY(0);
  opacity: 1;
}

.modal-wrapper {
  position: absolute;
  z-index: 9999;
  top: 6em;
  left: 50%;
  width: 32em;
  margin-left: -16em;
  background-color: #fff;
  box-shadow: 0 0 1.5em hsla(0, 0%, 0%, 0.35);
  padding: 0rem;
}

.modal-transition {
  transition: all 0.3s 0.12s;
  transform: translateY(-10%);
  opacity: 0;
}

.modal-header{
  padding: 1rem;
  background: #3b4b9f;
  color: #fff;
}

.modal-close {
  position: absolute;
  top: 0.52rem;
  right: 1rem;
  padding: 0em;
  color: #aaa;
  background: none;
  border: 0;
  font-size: 1.75rem;
  margin: 0px;
  padding: 0px;
  cursor:pointer;
}

.modal-heading{
  margin:0px;
  font-size: 1.25rem;
}

.modal-close:hover {
  color: #777;
}

.modal-body{
  max-height: 480px;
  overflow-y: auto;
}

.modal-body, .modal-footer{
  padding:1.25rem;
}


