.checkbox {
  display: block;
  width: 1.25rem;
  height: 1.25rem;
  border-style: solid;
  border-width: 1px;
  border-radius: 3px;
  position: relative;
  padding: 4px;
  background-color: rgba(143, 155, 179, 0.24);
  border-color: #8f9bb3;
  cursor: pointer;
}

.checkbox.active {
  background-color: #3366ff;
  border-color: #3366ff;
}

.check-ticked{
  display:none;
}

.checkbox.active > .check-ticked{
  display:block;
}
